  public fetchReviewList() {
    let resp;
    this._ProductService
      .getAllReviewList(localStorage.getItem("userId"))
      .subscribe((response: any) => {
        resp = response;
        this.reviewList = resp;
        console.log(this.reviewList, "reviewList");
        this.mapRatingwithProducts();
      });
  }

  mapRatingwithProducts() {
    this.orderList.forEach((order) => {
      this.reviewList.forEach((list) => {
        if (order.productId == list.productId) {
          console.log(
            "order.productId == list.productId",
            order.productId,
            list.productId,
            order.productId == list.productId
          );
          order.rating = list.rating;
        }
      });
    });

    console.log("this.orderList", this.orderList);
  }

  getColor(index: number, rating) {
    if (this.isAboveRating(index, rating)) {
      return "#DDDDDD";
    }

    return "gold";
  }

